@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Custom MesChain Styles */
.meschain-container {
  @apply min-h-screen bg-gray-50;
}

.meschain-card {
  @apply bg-white rounded-lg shadow-lg p-6;
}

.meschain-button {
  @apply px-4 py-2 rounded-md font-medium transition-colors;
}

.meschain-button-primary {
  @apply bg-blue-600 hover:bg-blue-700 text-white;
}

.meschain-button-secondary {
  @apply bg-gray-600 hover:bg-gray-700 text-white;
}

.meschain-button-success {
  @apply bg-green-600 hover:bg-green-700 text-white;
}

.meschain-button-danger {
  @apply bg-red-600 hover:bg-red-700 text-white;
}

.meschain-input {
  @apply w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500;
}

.meschain-select {
  @apply w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500;
}

.meschain-table {
  @apply min-w-full divide-y divide-gray-200;
}

.meschain-table-header {
  @apply px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider;
}

.meschain-table-cell {
  @apply px-6 py-4 whitespace-nowrap text-sm text-gray-900;
}

/* Loading animations */
.meschain-spinner {
  @apply animate-spin rounded-full border-b-2 border-blue-600;
}

/* Custom scrollbar */
.meschain-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.meschain-scrollbar::-webkit-scrollbar-track {
  background: #f1f1f1;
}

.meschain-scrollbar::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 4px;
}

.meschain-scrollbar::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* Responsive utilities */
@media (max-width: 640px) {
  .meschain-mobile-hidden {
    display: none;
  }
}

/* Chart container styles */
.chart-container {
  position: relative;
  height: 300px;
  width: 100%;
}

/* Modal styles */
.meschain-modal-overlay {
  @apply fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50;
}

.meschain-modal-content {
  @apply relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white;
}

/* Status badges */
.status-active {
  @apply bg-green-100 text-green-800;
}

.status-inactive {
  @apply bg-gray-100 text-gray-800;
}

.status-error {
  @apply bg-red-100 text-red-800;
}

.status-warning {
  @apply bg-yellow-100 text-yellow-800;
}

.status-syncing {
  @apply bg-blue-100 text-blue-800;
}

/* Gradient backgrounds */
.gradient-blue {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

.gradient-green {
  background: linear-gradient(135deg, #4facfe 0%, #00f2fe 100%);
}

.gradient-purple {
  background: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%);
}

.gradient-orange {
  background: linear-gradient(135deg, #ffecd2 0%, #fcb69f 100%);
} 